import form from "../controllers/form";

export default (app) => {

    /* Route for add form */
    app.route("/form/formStructure").post(form.saveStructure);

    /* Route for Update form */
    app.route("/form/update/:mongo_id").put(form.updateStructure);

    /*Route for fetching all forms structure*/
    app.route("/form/fetch").get(form.getAllForms);

    /*Route for fetching by id*/
    app.route("/form/fetchById/:mongo_id/:form_id").get(form.getFormById)

    /*Route for saving form data*/
    app.route("/form/updateData/:mongo_id").put(form.updateFromData)

    /*Route For insert*/
    app.route("/form/insertFormData").post(form.insertFormData)

    /*Route for serach form*/
    app.route("/form/search/:order/:page/:limit/:keyword/:sort").get(form.serachFromData)

    /*Route for remove form*/
    app.route("/form/delete/:id").delete(form.deleteFormData);

    /*get count of all data*/
    app.param('page', form.findAllCount)

    return app;
};